	// 导入自定义组件
	import trailerStars from '../../components/trailerStars.vue'
	export default {
		data() {
			return {
				carouselList: [],
				hotSuperheroList: [],
				hotTrailerList: [],
				animationData:{},
				guessULikeList: [],
				animationDataArr: [
					{}, {}, {}, {}, {}
				]
			}
		},
		onUnload() {
			// 页面卸载时，清除动画数据
			this.animationData = {},
			this.animationDataArr = [
				{}, {}, {}, {}, {}
			];
		},
		onPullDownRefresh() {
			this.refresh()
		},
		onLoad() {
			// 在页面创建的时候，创建一个临时动画对象

			this.animation = uni.createAnimation();

			uni.request({
              url: this.serverUrl + '/index/carousel/list?qq=38833964', 
			  method:'POST',
			  header:{
	            'content-type':'application/x-www-form-urlencoded'
	          },

    //           data:{
	   //           qq:'38833964'
				// },
              success: (res) => {
               // console.log(res.data);
                // 获取真实数据前，判断状态是否为200
				if (res.data.status === 200) {
					this.carouselList = res.data.data
				}
               }
            });
				// 查询热门超英
			uni.request({
				url: this.serverUrl + '/index/movie/hot?type=superhero&&qq=38833964',
				method: "POST",
				header:{
				  'content-type':'application/x-www-form-urlencoded'
				},
				// data:{
				// 	type:'superhero',
				//     qq:'38833964'
				// 				},
				success: (res) => {
					// console.log(res.data);
					// 获取真实数据之前，务必判断状态是否为200
					if (res.data.status == 200) {
						this.hotSuperheroList = res.data.data;
					}

				}
			});
			// 查询超英预告
			uni.request({
				url: this.serverUrl + '/index/movie/hot?type=trailer&&qq=38833964',
				method: "POST",
				header:{
				  'content-type':'application/x-www-form-urlencoded'
				},
				// data:{
				// 	type:'superhero',
				//     qq:'38833964'
				// 				},
				success: (res) => {
					// console.log(res.data);
					// 获取真实数据之前，务必判断状态是否为200
					if (res.data.status == 200) {
						this.hotTrailerList = res.data.data;
					}
			
				}
			});
			// 由于猜你喜欢会在下拉刷新时复用，所以不在onload中写，而是定义成一个方法
			this.refresh()
		},
		methods: {
			refresh() {
				uni.showLoading({
					mask: true
				});
				uni.showNavigationBarLoading();
				// 查询猜你喜欢数据列表
				uni.request({
					url: this.serverUrl + '/index/guessULike?qq=38833964',
					method: "POST",
					header:{
					  'content-type':'application/x-www-form-urlencoded'
					},
					// data:{
					// 	type:'superhero',
					//     qq:'38833964'
					// 				},
					success: (res) => {
						// console.log(res.data);
						// 获取真实数据之前，务必判断状态是否为200
						if (res.data.status == 200) {
							this.guessULikeList = res.data.data;
						}
				
					},
					complete: () => {
						uni.hideNavigationBarLoading();
						uni.hideLoading();
						uni.stopPullDownRefresh();
					}
				});
			},
           // 实现点赞动画效果

		   praiseme(e) {
			   // 通过dataset获得data数据，注意这里gindex要小写（只识别小写）
			   var gIndex = e.currentTarget.dataset.gindex;
			   console.log(gIndex)
			   // 构建动画数据，并且通过step表示这组动画的完成
			   this.animation.translateY(-60).opacity(1).step({
				   duration:500
			   });
			   // 导出动画数据到view组件，实现组件的动画效果
			   this.animationData = this.animation;
			   this.animationDataArr[gIndex] = this.animationData.export(); 
			   // 还原动画
			   setTimeout(function() {
				   this.animation.translateY(0).opacity(0).step({
					   duration:0
				   })
				   this.animationData = this.animation;
				   this.animationDataArr[gIndex] = this.animationData.export();
			   }.bind(this), 500);
		   }

		},
		components:{
			trailerStars
		}
	}
